ul{
display: flex;
flex-direction: row;
justify-content: flex-start;
align-content: center;
flex-wrap: wrap;
list-style: none;
}
li{
padding:  10px;
width: 250px;
max-width: 250px;
max-height: 400px;

}
.card{
    margin: auto;
padding: 5px;
width: 100%;
height: 100%;
box-shadow: 5px 5px 10px #ccc;
align-items: center;
display: flex;
flex-direction: column;
justify-content: center;
}
.card > img {
width: 85%;
height: 40%;
}
.card article {
padding: 5px;
}
.card h3 {
    padding: 4px;
}
.card p {
    padding: 3px;   
}

